:root {
    --container-back: #fff;
    --primary-dark: #f5f5f5;
    --primary: #fff;
    --accent: #3cb4e0;
    --progress-back: #f0f0f0;
    --extra: #a2a3a7;
    --button-background: #fff;
}

* {
    font-family: "Poppins", sans-serif;
}

.no-mt { margin-top: initial !important; }
.form-container {
    background-color: var(--container-back);
    max-width: 600px;
    width: 90%;
    margin: 60px auto 50px auto;
    font-size: 18px;
    color: black;
    border-radius: 12px;
    position: relative;
    z-index: 1000;
    box-shadow: 1px 1px 2px 1px #8080808f;
    height: max-content;
}

.form-header {
    text-align: center;
    font-size: 2.2rem;
    justify-content: center;
    background: var(--accent);
    border: 0;
    color: white;
    padding: 20px 20px;
    margin: 0;
	box-shadow: none;
	border-top-left-radius: 12px;
	border-top-right-radius: 12px;
}

.form-header2 { 
    font-size: 2rem; 
    font-family: Muli,Roboto,"Helvetica",Arial,sans-serif; 
    font-weight: 600; 
    line-height: 1.6; 
    padding: 20px; 
}

.btn-container { margin: 30px 0; width: 100%; text-align: center; }
.save-btn { width: 100px; height: 35px; margin-bottom: 30px; color: white; background: #039be5; }
.save-btn:hover { opacity: 0.8; }
.form-header-edit { padding: 0 20px; }
.section-containing-block { position: relative; }
.form-modify-link { position: absolute; z-index: 1; right: 20px; top: 18px; width: 80px; background: #0386ba; color: white; border-radius: 5px; height: 35px; padding-left: 30px; line-height: 33px; font-weight: bold; font-size: 1.5rem; }
.form-modify-link::before { position: absolute; content: '+'; left: 10px; font-size: 2.5rem; top: -1px; }
.form-modify-link:hover { opacity:  0.8; }

.form-back-link { display: inline-block; width: 35px; color: #0386ba; border: solid 2px; border-radius: 100%; height: 35px; font-weight: bold; font-size: 1.5rem; padding: 4px; vertical-align: sub; }

.row-container { display: flex; gap: 15px; }
.row-container .form-control { width: calc(100% - 50% - 10px); }

.form-edit-form { margin-top: 30px; }
.form-edit-form .MuiFormControl-root + * { margin-top: 15px; }
.form-edit-form .form-error { color: red; }

.form-edit-form label { margin-bottom: 5px; }
.form-edit-form label .optional { color: grey; }

.form-edit-form .form-input { padding: 5px; }
.form-edit-form .MuiSelect-outlined { padding-top: 10.5px; padding-bottom: 10.5px; }

.form-edit-form .btn-container { margin: 30px 0; width: 100%; text-align: center; }
.form-edit-form .clear-btn { width: 100px; height: 35px; color: white; background: #ec294a; }
.form-edit-form .trim-btn { width: 100px; height: 35px; color: white; background: #4d0bb8; }
.form-edit-form .save-btn { width: 100px; height: 35px; color: white; background: #039be5; }
.form-edit-form .save-btn:hover { opacity: 0.8; }
.form-edit-form .save-btn-secondary { color: white; background: #1e2125; margin-left: 15px; width: 115px; }

.form-question-section {
    width: 100%;
    padding: 25px;
}
.form-question-number {
    color: var(--extra);
    text-align: start;
    margin-bottom: 5px;
    font-size: 1.5rem;
}
.form-question {
    font-weight: normal;
    text-align: left;
    width: 100%;
    color: black;
    margin-bottom: 15px;
    font-size: 1.8rem;
}
.form-sub-question {
    font-weight: normal;
    text-align: left;
    width: 100%;
    color: black;
    margin-bottom: 5px;
    font-size: 1.5rem;
}
.form-question:first-child {
    margin-top: 0;
}
.form-option-container {
	width: 80%;
	margin: 20px auto;
}
.form-option {
	display: block;
	width: 100%;
/*	width: 48%; */
	background: gainsboro;
	border-radius: 5px;
	transition: all 0.1s ease-out;
	font-size: 15px !important;
	padding: 10px;
}
.form-option + * {
	margin-top: 10px;
}
.form-option:hover, .form-option:focus {
	outline: none;
	background: var(--accent);
	color: white;
}

.li {
    margin: 0 15px;
    width: 100%;
	font-size: 12px !important;
    list-style-type: disc;
}
.para { margin-top: 15px; font-size: 1.5rem; }

.disable { background: gainsboro !important; color: initial !important; cursor: initial; }
.selected { background: #1e2125; color: white; }
.correct { background: #25d366 !important; color: black; cursor: initial; }
.wrong { background: #ff3d00 !important; color: white; cursor: initial; }
.form-control, .date-control {
	margin-bottom: 30px;
}

.mt-20 { margin-top: 20px; }

#resultHeading { font-size: 2.7rem; margin-bottom: 10px; }
#resultHeading + p { color: grey; }

.form-result {
	background: #1e2125;
	font-size: 3rem;
	text-align: center;
	padding: 15px;
	color: white;
	border-radius: 5px;
	margin-bottom: 25px;
}
.form-result span span.safe {
	color: #14ff00;
}
.form-result span.watch {
	color: #3cb4e0;
}
.form-result span.vulnerable {
	color: #ffff28;
}
.form-result span.emergency {
	color: #ff3d00;
}
.form-result + div {
	text-align: center;
	margin-bottom: 20px;
}
.m1 {
	margin-top: 40px;
}
.m2 {
    margin-top: 30px;
    margin-bottom: 0;
}
.m3-1 {
    margin-top: 0;
    margin-bottom: 15px;
    color: red;
}
.m3 {
    margin-top: 0;
}
.m4 {
    margin: auto;
    width: 120px;
}
.mb-20 { margin-bottom: 20px; }
.mt-10 { margin-top: 10px; }
.mt-20 { margin-top: 20px; }
.mb-10 { margin-bottom: 10px; }
.date-control { display: inline-block; max-width: 300px; margin-bottom: 10px; margin-top: 0; margin-left: 10px; }

.pivot-chart-container { padding: 25px; background: white; }
.pivot-chart-container>section + section { margin-top: 40px; }
.pivot-chart-container>section>h2 { margin-bottom: 10px; }

.chart-select--control { width: 100%; }
.chart-select--control + .chart-select--control { margin-top: 10px; }
.pivot-analytics-date-control { display: inline-block; max-width: 300px; margin-bottom: 10px; margin-top: 0; margin-left: 10px; }

.chart-container { margin-top: 30px; }

.form-info {
	padding: 24px;
	border: solid 1px grey;
    border-radius: 5px;
    padding-left: 50px;
    margin-bottom: 25px;
}
.form-info-correct, .form-info-wrong { position: relative; }
.form-info-correct::before {
	content: '\2023';
	position: absolute;
	left: -30px;
	top: -25px;
	font-size: 5.3rem;
    font-weight: bold;
    color: #25d366;
}
.form-info-wrong::before {
    content: '\2A2F';
	position: absolute;
	left: -32px;
	top: -14px;
	font-size: 3.3rem;
    font-weight: bold;
    color: #ff3d00;
}
.article-img-header { text-align: left; width: 100%; color: #424242; margin-bottom: 5px; font-size: 1.4rem; }
.article-img-control { display: flex; }
.article-img-container { position: relative; width: calc(100% - 150px); }

.article-img-container .img-label { margin-bottom: 10px; display: block; width: 100%; font-size: 16px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.article-img-container .img-btn { display: inline-block; vertical-align: top; margin-top: -5px; }
.article-img-container .img-delete-label { display: inline-block; vertical-align: top; margin-top: -5px; margin-left: 10px; }  
.article-img-container .img-delete-btn { width: 35px; min-width: 0; } 
.article-img-preview { width: 150px; height: auto; max-height: 100px;}
.article-img-preview img { width: 100%; height: 100%; object-fit: contain; }

.form-image-container {
	height: 250px;
	border: dashed 2px grey;
	padding: 10px;
	width: max-content;
	margin: auto;
	margin-bottom: 20px;
}
.form-image-container img {
    height: 100%;
    display: block;
}
.form-action-btn {
    background: #3cb4e0;
    opacity: 1;
    color: white;
    display: inline-block;
}
.form-action-btn:hover {
    opacity: 0.8;
}

.form-action-btn-last { background: #1e2125; margin-left: 2%; }
.form-action-btn-last:hover { background: #1e2125; opacity: 0.8; }

.form-support + * { margin-top: 25px; }
.form-support-header-container { position: relative; }
.form-support-header { font-size: 15px; }
.remove-box { position: absolute; top: 0; right: 12px; opacity: 0.7; }
.remove-box:hover, .remove-box:focus { opacity: 1; cursor: pointer; }
.remove-box::before {
	content: '';
	width: 48px;
	height: 48px;
	background: transparent;
	display: block;
	border-radius: 30px;
	position: absolute;
	top: -12px;
	left: -12px;
	z-index: -1;
}
.remove-box:hover::before, .remove-box:focus::before { background: #0000003d; }
.cacr-img-container { width: 100px; margin: 20px auto; } 
.cacr-img-container img { width: 100%; display: block; }

.nsa-img-container { position: relative; }
.nsa-img-container .img-label { margin-bottom: 10px; display: inline-block; width: calc(100% - 150px); font-size: 19px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.nsa-img-container .img-btn-block { position: absolute; top: 0; right: 0; }
.nsa-img-container .img-btn { display: inline-block; vertical-align: top; margin-left: 10px; margin-top: -5px; }
.nsa-img-container .img-delete-label { display: inline-block; vertical-align: top; margin-top: -5px; margin-left: 10px; }
.nsa-img-container .img-delete-btn { width: 35px; min-width: 0; } 

.photo-header { font-size: 16px; font-weight: 700; margin-top: 20px; margin-bottom: 20px; color: #303a4b; }
.img-section p { font-size: 12px; color: #99b2bf; }
.img-section .subheader { margin-top: 10px; margin-bottom: 20px; }
.img-section .img-list { display: flex; flex-wrap: wrap; margin-bottom: 20px; gap: 5px; }
.img-section .img-container { width: 80px; height: 80px; border-radius: 5px; overflow: hidden; position: relative; }
.img-section .img-container img { width: 100%; height: 100%; }
.img-section .img-container .remove-img { position: absolute; top: 2px; right: 2px; color: white; background: #0009; width: 25px; padding: 0; min-width: 0; height: 25px; }
.img-section .img-container .remove-img svg { width: 15px; }
.img-section .img-container .remove-img:hover,
.img-section .img-container .remove-img:focus { background: black; }
.img-section .img-container .rotate-img { position: absolute; top: 50%; left: 50%; color: white; background: #0009; width: 25px; padding: 0; min-width: 0; transform: translate(-12px, -12px); height: 25px;}
.img-section .img-container .rotate-img svg { width: 15px; }
.img-section .img-container .rotate-img:hover,
.img-section .img-container .rotate-img:focus { background: black; }
.img-section .img-select-btn { background: #99b2bf; width: 80px; height: 80px; line-height: 80px; text-align: center; border-radius: 40px; color: white; font-size: 40px; font-weight: normal; }
.img-section .img-select-btn:hover, .img-section .img-select-btn:focus { opacity: 0.8; }
.img-section .img-select-btn:disabled { background: #dbdbdb; }
.img-section .img-error { color: red; margin-bottom: 20px; }

.refresh-btn {
	position: absolute;
	right: 10px;
	top: 25px;
	opacity: 0.6;
}
.refresh-btn:hover { opacity: 1; }
.rotate-infinite {
	animation: rotate 1.5s linear infinite;
}

@keyframes rotate {
	to {
		transform: rotate(-360deg);
	}
} 

.tab-list-block { border-bottom: solid 1px #ccc; height: 60px; display: flex; align-items: flex-end; justify-content: flex-end; }
.tab-list-block + section { padding-top: 20px; }
.tab-list-block .tab-list-item { background: #e3e3e3; min-width: 100px; text-align: center; height: 50px; line-height: 50px; vertical-align: bottom; transition: all .1s ease-in; }
.tab-list-block .tab-list-item.active { color: white; height: 60px; background: #1e2125 !important; }
.tab-list-block .tab-list-item:hover, .tab-list-block .tab-list-item:focus-within { background: #3cb4e0; color: white; }
.tab-list-block .round-corner-left { border-top-left-radius: 5px; }
.tab-list-block .round-corner-right { border-top-right-radius: 5px; }

.q-container h3 { font-weight: bold; }
.q-container label.grey { margin-bottom: 5px; color: grey; font-size: 13px; }
.q-container .form-error { margin-top: 5px !important; }
.q-container .MuiRadio-root { padding: 0 9px; }

.option-header { margin-top: 20px; margin-bottom: 10px; }
.option-table-container { width: 100%; border: solid 1px #cccccc; border-radius: 5px; }
.option-table { width: 100%; }
.option-table thead th { padding: 10px; background: #039be5; color: white; }

.option-table .num-col { width: 50px; }
.option-table .action-col { width: 50px; }
.option-table .option-col { text-align: left; }
.option-table .score-col { width: 80px; }
.option-table .label-col { width: calc(50% - 50px); }

.option-table .dqa-num-col { width: 30px; text-align: center; }
.option-table .commodity-col { width: 170px; text-align: left; }
.option-table .bgn-stock-col { width: calc((100% - 200px)*20%); text-align: center; }
.option-table .rcv-stock-col { width: calc((100% - 200px)*17%); text-align: center; }
.option-table .dist-stock-col { width: calc((100% - 200px)*20%); text-align: center; }
.option-table .exp-stock-col { width: calc((100% - 200px)*16%); text-align: center; }
.option-table .end-stock-col { width: calc((100% - 200px)*14%); text-align: center; }
.option-table .diff-col { width: calc((100% - 200px)*13%); text-align: center; }

.option-table .MuiOutlinedInput-notchedOutline { border: none !important; }

.option-table td { border: solid 1px #cccccc; }
.option-table td:focus-within { border: solid 2px black; }
.option-table .error-col { border: solid 2px red !important; }

.option-table .add-btn { width: 100%; color: #0386ba; }
.option-table .add-btn .MuiButton-label { justify-content: left; text-transform: none; }
.option-table .add-icon { width: 100px; display: inline-block; text-align: right; padding-right: 22px; }
.option-table .delete-btn:hover, .option-table .delete-btn:focus { color: red; }

.help-icon { position: relative; vertical-align: middle; margin-left: 5px; }
.help-icon::before { position: absolute; content: 'Leave this blank to associate with every facility type'; background: #1e2125; padding: 10px; font-family: Muli, Roboto, Helvetica Neue, Arial, sans-serif; font-size: 1rem; top: -40px; left: -120px; color: white; border-radius: 3px; display: none; }
.help-icon::after { position: absolute; width: 10px; height: 10px; content: ' '; background: #1e2125; transform: rotate(45deg); top: -15px; left: 7px; display: none; }
.help-icon:hover, .help-icon:focus { cursor: pointer; color: black !important; }
.help-icon:hover::before, .help-icon:focus::after { display: block; }

.custom-pos { position: absolute; top: 30px; right: 30px; width: calc(100% - 60px); }

.preview-header::before { content: 'Below box contains the preview of the question.'; }
.preview-block { padding: 20px; border: dashed 3px #ccc; }
.preview-tab .validate-btn { width: 150px; height: 30px; background: #039be5; color: white; margin-top: 20px; }
.preview-tab .score-block { margin-top: 15px; font-size: 1.5rem; width: 150px; }
.preview-tab .score-block span { text-align: right; width: 100px; display: inline-block; }

.custom-logic-block + .custom-logic-block { margin-top: 20px; }
.custom-logic-block .logic-header { margin-top: 0px; margin-bottom: 10px; }
.custom-logic-block .fun-container { padding: 10px; border: solid 2px #ccc; font-family: monospace; background: #f2f2f2; }
.custom-logic-block .fun-container p { font-family: monospace; margin: 5px 0; font-size: 1.4rem; font-weight: bold; color: grey; letter-spacing: 0.14rem; }
.custom-logic-block .body-container { padding: 7px 10px; margin-left: 30px; width: calc(100% - 50px); font-family: monospace;height: 200px; }

.section-container { padding: 20px; width: 100%; }
.section-container .facitlity-detail { width: 100%; }
.section-container .img-container { width: 100px; height: 100px; border-radius: 100%; overflow: hidden; }
.section-container .img-container img { width: 100%; height: 100%; display: block; }
.section-container .detail-section { vertical-align: top; margin-left: 20px; margin-top: 5px; }
.section-container .name-header { font-weight: bold; }
.section-container .secondary-header { font-weight: bold; margin-top: 30px; margin-bottom: 15px; }
.section-container .third-header {font-weight: bold;margin-top: 10px;margin-bottom: 10px; font-size: 1.5rem; }

.section-container .supervision-list-block { margin-top: 20px; width: 100%; counter-reset: sectionCounter;}
.section-container .section-item { padding: 5px 15px 5px 50px; border: solid 1px #ccc; border-radius: 5px; counter-increment: sectionCounter; position: relative; }
.section-container .section-item:hover, .section-container .section-item:focus { border-color: var(--accent); border-width: 2px; outline: none; }
.section-container .section-item + .section-item { margin-top: 10px; }
.section-container .section-item::before { position: absolute; content: counter(sectionCounter); left: 0; top: 0; font-size: 1.5rem; display: block; width: 50px; height: 50px; text-align: center; line-height: 50px; }
.section-container .count-span, .section-container .score-span { color: grey; display: inline-block; margin-bottom: 5px; }
.section-container .score-span { float: right; }
.section-container .custom-pos { position: absolute; width: calc(100% - 60px); top: 150px; }

.pdf-table-container { width: 100%; border: solid 1px #cccccc; border-radius: 5px; }
.pdf-table { width: 100%; }
.pdf-table thead th { padding: 10px; background: #039be5; color: white; text-align: center; }

.pdf-table .id-col { width: 5%; }
.pdf-table .text-col { width: 35%; }
.pdf-table .score-col { width: 10%; }
.pdf-table .value-col { width: 50%; }

.pdf-table .MuiOutlinedInput-notchedOutline { border: none !important; }

.pdf-table td { border: solid 1px #cccccc; }
.pdf-table td:focus-within { border: solid 2px black; }

.iss-pdf-table-container { width: 100%; border: solid 1px #cccccc; border-radius: 5px; }
.iss-pdf-table { width: 100%; }
.iss-pdf-table thead th { padding: 10px; background: #039be5; color: white; text-align: center; }
.iss-pdf-table .id-col { width: 5%; }
.iss-pdf-table .text-col { width: 50%; }
.iss-pdf-table .score-col { width: 10%; }
.iss-pdf-table .value-col { width: 35%; }

.iss-pdf-table .MuiOutlinedInput-notchedOutline { border: none !important; }

.iss-pdf-table td { border: solid 1px #cccccc; }
.iss-pdf-table td:focus-within { border: solid 2px black; }

@media (max-width: 500px) {
    .tab-list-block > ul { text-align: left !important; white-space: nowrap; overflow: scroll; -ms-overflow-style: thin; scrollbar-width: thin; }
    .tab-list-block > ul::-webkit-scrollbar { display: none; }
    .row-container .form-control { width: 100% }
}

@media (max-width: 768px) {
    .row-container { display: block; }
    .form-edit-form .MuiFormControl-root + * { margin-top: 15px !important; }
    .row-container .form-control { width: 100% }
}

@media (max-width: 650px) {
    .custom-pos { position: initial; width: 100%; justify-content: start !important; }
    .row-container .form-control { width: 100% }
    
}

@media (max-width: 420px) {
    .score-col { width: 40px; font-size: 12px; }
    .num-col { width: 30px; font-size: 12px; }
    .option-col { font-size: 12px; }
    .field-col { font-size: 12px; }
    .label-col { font-size: 12px; }
    .row-container .form-control { width: 100% }
}

@media only screen and (min-width: 768px) {
    .chart-select--control { width: 300px; margin-right: 20px; }
    .chart-select--control + .chart-select--control { margin-top: 0; }
}

.tree-container li { word-break: unset !important; }